export default {
  url: "reports/lists/factors",
  routerName: 'FactorForm',
  cols: [{
      text: "",
      value: "code",
      type: "number",
      filters: ["code__icontains"]
    },
    {
      text: "",
      value: "date",
      type: "date",
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      text: "",
      value: "time",
      type: "time",
      filters: [{
          label: " ",
          model: "time__gte"
        },
        {
          label: " ",
          model: "time__lte"
        }
      ]
    },
    {
      text: "",
      value: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      text: "",
      value: "explanation",
      type: "text",
      filters: ["explanation__icontains"]
    },
    {
      text: "  ",
      value: "total_sum",
      type: "numeric",
      sortable: false,

    },
    {
      text: "",
      value: "is_definite",
      type: 'boolean',
    },
    {
      text: " ",
      value: 'has_tax',
      type: 'boolean',
    }
  ],
}