export default {
  url: "reports/lists/cheques",
  routerName: 'ChequeDetail',
  cols: [{
      text: "",
      value: "serial",
    },
    {
      text: "",
      value: "explanation",
    },
    {
      text: "",
      value: "date",
    },
    {
      text: " ",
      value: "due",
    },
    {
      text: "",
      value: "value",
    },
    {
      text: " ",
      value: "account.name",
    },
    {
      text: "",
      value: "status",
      items: [{
          text: '',
          value: 'blank'
        },
        {
          text: ' ',
          value: 'notPassed'
        },
        {
          text: ' ',
          value: 'inFlow'
        },
        {
          text: ' ',
          value: 'passed'
        },
        {
          text: '',
          value: 'bounced'
        },
        {
          text: '',
          value: 'cashed'
        },
        {
          text: ' ',
          value: 'revoked'
        },
        {
          text: '',
          value: 'transferred',
        }
      ],
    },
    {
      text: " ",
      value: "bankName",
      type: "text",
      filters: ["bankName__icontains"]
    }
  ],
}